<template>
	<div id="statcenter-order-detail">
		<public-header title="营业统计详情"></public-header>
		<div class="content">
			<div class="text-center">
				<van-row class="font-14 c-gray flex-lr">
					<van-col span="8" class="c-danger">对账单以天为单位</van-col>
					<van-col span="6">应收金额</van-col>
					<van-col span="6">应付金额</van-col>
					<van-col span="4"></van-col>
				</van-row>
				<van-row class="font-14 c-gray flex-lr">
					<van-col span="5">账期</van-col>
					<van-col span="3">完成单数</van-col>
					<van-col span="6">用户支付<br>平台补贴</van-col>
					<van-col span="6">补贴(商家)<br>平台服务费<br>平台配送费</van-col>
					<van-col span="4">实际到账</van-col>
				</van-row>
				<van-row class="font-14 flex-lr" v-for="(item, index) in records" :key="index">
					<van-col span="5">{{item.stat_day}}</van-col>
					<van-col span="3">{{item.total_success_order}}</van-col>
					<van-col span="6" class="c-primary">+¥{{item.final_fee}}<br>+¥{{item.plateform_discount_fee}}</van-col>
					<van-col span="6" class="c-danger">
						-¥{{item.store_discount_fee}}<br>
						-¥{{item.plateform_serve_fee}}<br>
						-¥{{item.plateform_delivery_fee}}
					</van-col>
					<van-col span="4" class="c-primary font-bold">¥{{item.store_final_fee}}</van-col>
				</van-row>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				days: {},
				stat_day: {
					start: '',
					end: ''
				},
				records: [],
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				var query = that.util.parseQuery(that.$route.query);
				that.days = query.days;
				that.stat_day.start = query.start;
				that.stat_day.end = query.end;
				getData({
					vue: that,
					url: 'manage/statcenter/order/detail',
					data: {
						days: that.days,
						stat_day: that.stat_day
					},
					success: (res) => {
						that.records = [...that.records, ...res.records];
					}
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#statcenter-order-detail .van-row:nth-child(2n+1){
		background-color: #fff;
	}
	#statcenter-order-detail .van-row:nth-child(2n){
		background-color: #eee;
	}
	#statcenter-order-detail .van-row:first-child{
		background-color: #eee;
		border-bottom: 1px solid #fefefe;
	}
	#statcenter-order-detail .van-row .van-col{
		font-size: 13px;
		padding: 5px 0;
		min-height: 64px;
		line-height: 1.2;
		display: flex;
		align-items: center;
		justify-content: center;
		border-right: 1px solid #fefefe;
	}
	#statcenter-order-detail .van-row:first-child .van-col{
		min-height: 36px;
	}
	#statcenter-order-detail .van-row .van-col:last-child{
		border: 0px;
	}
</style>


